import { Entity, NgField } from '@farris/devkit';
/**
 * 客户实体
 */
export class SysPosEntity extends Entity {

  /**
   * Id
   */
  @NgField({
    dataField: 'id',
    primary: true
  })
  ID: string;

  /**
   * code
   */
  @NgField({
    dataField: 'code'
  })
  Code: string;

  /**
   * name
   */
  @NgField({
    dataField: 'name'
  })
  Name: string;


  /**
   * 分级码
   */
  @NgField({
    dataField: 'path'
  })
  Path: string;


    /**
   * 层级
   */
  @NgField({
    dataField: 'layer'
  })
 Layer: string;


  /**
   * 明细
   */
  @NgField({
    dataField: 'isDetail'
  })
  Isdetail: boolean;



  /**
   * 排序
   */
  @NgField({
    dataField: 'sortOrder'
  })
  Sortorder: string;

  /**
   * 是否系统预置
   */
  @NgField({
    dataField: 'sysInit'
  })
  SysInit: string;

  /**
   * 描述
   */
  @NgField({
    dataField: 'note'
  })
  Note: string;


  /**
   * 父节点  //NgField、NgList、NgObject
   */
  @NgField({
    dataField: 'parentId',
  })
  Parentid: string;
}



