
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { SysPosUistate } from '../sys-post-uistate';
import { SysPosStatemachine } from '../sys-post-statemachine';


@Injectable()
@NgCommandHandler({
  commandName: 'edit'
})
class EditHandler extends CommandHandler {

  constructor(
    private uiState: SysPosUistate,
    private stateMachine: SysPosStatemachine
  ) {
    super();
  }

  schedule() {

    // 状态迁移
    this.addTask('transitState', () => {
      this.uiState.action = 'edit';
      this.stateMachine['edit']();
    });
  }
}

export { EditHandler };



