import { Injector, Injectable } from '@angular/core';
import { NgViewModel, ViewModel,  NgCommand } from '@farris/devkit';
import { SysPosBindingdata } from './sys-post-bindingdata';
import { SysPosForm } from './sys-post-form';
import { SysPosStatemachine } from './sys-post-statemachine';
import { SysPosUistate } from './sys-post-uistate';


/**
 * 视图模型
 */
@Injectable()
@NgViewModel({
  children: [],
  binding: SysPosBindingdata,
  form: SysPosForm,
  stateMachine: SysPosStatemachine,
})
class SysPosViewmodel extends ViewModel {

  public uiState: SysPosUistate;
  public stateMachine: SysPosStatemachine;

  /**
   * 构造函数
   */
  constructor(injector: Injector) {
    super(injector);
    this.uiState = this.injector.get(SysPosUistate);
  }

  /**
   * 表单加载
   */
  @NgCommand({
    name: 'formLoad'
   })
  public formLoad() {}

  /**
   * 新增子级
   */
  @NgCommand({
    name: 'addchild'
   })
  public addchild() {}

  /**
   * 新增平级
   */
  @NgCommand({
    name: 'additem'
   })
  public additem() {}

  /**
   * 编辑
   */
  @NgCommand({
    name: 'edit'
   })
  public edit() {}


  /**
   * 删除
   */
  @NgCommand({
    name: 'remove'
   })
  public remove() {}

  /**
   * 保存
   */
  @NgCommand({
    name: 'save'
   })
  public save() {}

  /**
   * 取消
   */
  @NgCommand({
    name: 'cancel'
   })
  public cancel() {}


}

export { SysPosViewmodel };



