import { Routes, RouterModule } from '@angular/router';
import { SysPostComponent } from './sys-post.component';
import { NgModule } from '@angular/core';


const routes: Routes = [
    {path: '', component: SysPostComponent}
];

@NgModule({
    imports: [
        RouterModule.forChild(routes)
    ],
    exports: [
        RouterModule
    ]
})

export class SysPostRoutingModule { }



