
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, EntityFactory } from '@farris/devkit';
import { DataUtil } from '../../../shared/data-util';
import { SysPosRepository, SysPosEntity } from '../../domain';
import { SysPosUistate } from '../sys-post-uistate';
import { SysPosStatemachine } from '../sys-post-statemachine';
import { SysPosBindingdata } from '../sys-post-bindingdata';


@Injectable()
@NgCommandHandler({
  commandName: 'additem'
})
class AddItemHandler extends CommandHandler {

  constructor(
    private uiState: SysPosUistate,
    private stateMachine: SysPosStatemachine,
    private repository: SysPosRepository,
    private bindingData: SysPosBindingdata
  ) {
    super();
  }

  schedule() {

    // 数据加载
    this.addTask('additem', () => {
      this.uiState.action = 'add';
      const item = this.bindingData.list.currentItem;
      // data必须与实体绑定数据大小写一致
      const data = {
          id: DataUtil.createNM(),
          code : '',
          name: '' ,
          layer : item['Layer'] || 1,
          isDetail: true,
          parentId: item['Parentid']
        };

      const newEntity = EntityFactory<SysPosEntity>(SysPosEntity, data);

      this.repository.addEntity(newEntity);
    });

    // 状态迁移
    this.addTask('transitState', () => {
      this.stateMachine['edit']();
    });
  }
}

export { AddItemHandler };



