import { Injectable, Injector } from '@angular/core';
import { NgRepository, Repository } from '@farris/devkit';
import { SysPosDatastorage } from './sys-post.datastorage';
import { SysPosEntity } from './entities/sys-post.entity';


@Injectable()
@NgRepository({
  from: SysPosDatastorage,
  entityType: SysPosEntity
})
class SysPosRepository extends Repository<SysPosEntity> {
  constructor(injector: Injector) {
    super(injector);
  }
}

export { SysPosRepository };



