
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { SysPosDataService } from '../../domain';
import { SysPosBindingdata } from '../sys-post-bindingdata';
import { SysPosStatemachine } from '../sys-post-statemachine';
import { SysPosUistate } from '../sys-post-uistate';

@Injectable()
@NgCommandHandler({
  commandName: 'save'
})
class SaveHandler extends CommandHandler {

  constructor(
    private dataService: SysPosDataService,
    private bindingData: SysPosBindingdata,
    private uiState: SysPosUistate,
    private stateMachine: SysPosStatemachine
  ) {
    super();
  }

  schedule() {

    // 数据加载
    this.addTask('save', () => {

        const item = this.bindingData.list.currentItem;
        this.dataService.save(item);
        this.uiState.action = 'save';
    });

    // 状态迁移
    this.addTask('transitState', () => {
      this.stateMachine['view']();
    });
  }
}

export { SaveHandler };



