import { Component, OnInit } from '@angular/core';
import { SysPosViewmodel } from './app';
import { SysPosDataService } from './domain';
import { FrameworkService } from '@gsp-sys/rtf-common';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Change, ChangeType } from '@farris/devkit';


@Component({
    selector: 'app-sys-post',
    templateUrl: 'sys-post.coponent.html',
    styleUrls: ['sys-post.coponent.css']
})
export class SysPostComponent implements OnInit {

    data: any;

    constructor(
        public viewmodel: SysPosViewmodel,
        private route: ActivatedRoute,
        private postDataservice: SysPosDataService,
        private frmService: FrameworkService
    ) { }

    ngOnInit() {
        this.route.paramMap.subscribe(
            (params: ParamMap) => {
                if (params.has('funcid')) {
                    const funcid = params.get('funcid');
                    this.postDataservice.sessionid = this.frmService.getSession(funcid);
                }
            });

        this.viewmodel.bindingData.changes.subscribe((changes: Change) => {
                console.log('changetype =' + changes.type);
                if (changes.type === ChangeType.SelectionChanged) {
                   return;
                }
                this.data = this.viewmodel.bindingData.list.toJSON();
            });
        this.viewmodel.form.valueChanges.subscribe(
                (changes: Change ) => {
                  console.log('changetype =' + changes.type);
                  this.data = this.viewmodel.bindingData.list.toJSON();
                  console.log(JSON.stringify(this.data));
            });
        this.viewmodel.formLoad();
    }

    handleSelection({ dataItem }: any): void {
      this.viewmodel.bindingData.list.setCurrentId(dataItem.ID);
    }
}



