
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler } from '@farris/devkit';
import { SysPosRepository } from '../../domain';
import { SysPosUistate } from '../sys-post-uistate';
import { SysPosStatemachine } from '../sys-post-statemachine';
import { SysPosBindingdata } from '../sys-post-bindingdata';

@Injectable()
@NgCommandHandler({
  commandName: 'cancel'
})
class CancleHandler extends CommandHandler {

  constructor(
    private repository: SysPosRepository,
    private bindingData: SysPosBindingdata,
    private stateMachine: SysPosStatemachine,
    private uiState: SysPosUistate,
  ) {
    super();
  }

  schedule() {

    // 数据加载
    this.addTask('cancel', () => {
        if (this.uiState.action !== 'add') {
            return ;
        }
        const id = this.bindingData.list.currentId;
        this.repository.removeEntityById(id.toString());
        this.uiState.action = 'camcel';
      });


    // 状态迁移
    this.addTask('transitState', () => {
      this.stateMachine['view']();
    });
  }
}

export { CancleHandler };



