import { Injectable } from '@angular/core';
import { HttpHeaders } from '@angular/common/http';
import { RestfulService } from '@farris/devkit';
import { SysPosRepository } from './sys-post.repository';
import { SysPosDatastorage } from './sys-post.datastorage';

@Injectable()
export class SysPosDataService {

  private url = '/api/runtime/sys/v1.0/posgroups';
  public sessionid: string;
  postdata: any;

  constructor(private reposity: SysPosRepository,
    private datastorage: SysPosDatastorage,
    private rest: RestfulService
  ) { }

  getdata(): any {
    const headers = new HttpHeaders().set('SessionId', this.sessionid);
    return this.rest.get(`${this.url}/${'all'}`, null, {headers}).subscribe((res: any) => {
      if (res instanceof Array) {
        res.forEach((item) => {
          if (item.parentId === '0') {
            item.parentId = undefined;
          }
        });
        this.datastorage.load(res);
      } else {
        if (res.parentId === '0') {
          res.parentId = undefined;
        }
        this.datastorage.load([res]);
      }
    });
  }



  /**
   * 保存
   */
  save(item: any) {

    // todo:此处争取顺序应该是先httpservice 更新服务端，然后客户端重新load
    const entities =  this.reposity.getAllEntities();
    entities.forEach(element => {
      if ( element.ID === item.ID) {
        element.Code = item.Code;
        element.Isdetail = item.Isdetail;
        element.Layer = item.Layer;
        element.Name = item.Name;
        element.Note = item.Note;
        element.Parentid = item.Parentid;
        element.Path = item.Path;
        element.Sortorder = item.Sortorder;
        element.SysInit = item.SysInit;
        this.postdata = this.convert2json(item);
      }
    });

    const headers = new HttpHeaders().set('SessionId', this.sessionid);
    this.rest.post(this.url, this.postdata, null, { headers }).subscribe(
      (res: any) => { }
    );
  }


  /**
   * 删除并重新加载
   */
  remove(id: string) {
      // todo:此处争取顺序应该是先httpservice 更新服务端，然后客户端重新load
      const headers = new HttpHeaders().set('SessionId', this.sessionid);
      this.rest.delete(`${this.url}/${id}`, null, {headers}).subscribe(
        (res: any) => {
          this.reposity.removeEntityById(id);
        }
      );
    }

    convert2json(item: any): any {

      const data = {
        Id: item.ID,
        Code: item.Code,
        Name: item.Name,
        Path: item.Path || '',
        ParentId: item.Parentid || '0',
        Layer: item.Layer || 1,
        Note: item.Note,
        IsDetail: item.Isdetail || true,
        Creator: ''
      };
      return data;
    }

  clonefunction(item: any) {

  }
}



