import { Injectable } from '@angular/core';
import { StateMachine, NgStatemachine, State, NgState, RenderState,  NgRenderState, Action, NgAction } from '@farris/devkit';

@Injectable()
@NgStatemachine()
class SysPosStatemachine extends StateMachine {

  /**
   * --------------------------------------------------------------------------------
   * 基本状态定义
   * --------------------------------------------------------------------------------
   */

  /**
   * 查看状态
   */
  @NgState({ initialState: true })
  viewState: State;

  /**
   * 新增状态
   */
  @NgState()
  addState: State;

  /**
   * 编辑状态
   */
  @NgState()
  editState: State;


  /**
   * --------------------------------------------------------------------------------
   * 渲染状态定义
   * --------------------------------------------------------------------------------
   */

  /**
   * 新增按钮是否允许点击
   */
  @NgRenderState({
    render: (context) => context.state === 'viewState'
  })
  canAdd: RenderState;

  /**
   * 编辑按钮是否允许点击
   */
  @NgRenderState({
    render: (context) => context.state === 'viewState'
  })
  canEdit: RenderState;


  /**
   * 取消按钮是否允许点击
   */
  @NgRenderState({
    render: (context) => context.state === 'addState' || context.state === 'editState'
  })
  canCancel: RenderState;


  /**
   * 删除
   */
  @NgRenderState({
    render: (context) => context.state === 'viewState'
  })
  canDelete: RenderState;

  /**
   * 保存按钮是否允许点击
   */
  @NgRenderState({
    render: (context) => context.state === 'addState' || context.state === 'editState'
  })
  canSave: RenderState;

  /**
   * 上一条、下一条是否允许点击
   */
  @NgRenderState({
    render: (context) => context.state === 'viewState'
  })
  canSwitch: RenderState;

  /**
   * 输入控件是否允许输入
   */
  @NgRenderState({
    render: (context) => context.state === 'addState' || context.state === 'editState'
  })
  canInput: RenderState;


  /**
   * --------------------------------------------------------------------------------
   * 迁移动作定义
   * --------------------------------------------------------------------------------
   */
  @NgAction({transitTo: 'addState' })
  add: Action;

  @NgAction({ transitTo: 'editState' })
  edit: Action;

  @NgAction({ transitTo: 'viewState' })
  view: Action;
}

export { SysPosStatemachine };



