import { Provider } from '@angular/core';
import { SysPosBindingdata } from './sys-post-bindingdata';
import { SysPosForm } from './sys-post-form';
import { SysPosStatemachine } from './sys-post-statemachine';
import { SysPosViewmodel } from './sys-post-viewmodel';
import { SysPosUistate } from './sys-post-uistate';
import { commandHandlers } from './handler';


const APP_PROVIDERS: Provider[] = [
    SysPosBindingdata,
    SysPosForm,
    SysPosStatemachine,
    SysPosViewmodel,
    SysPosUistate
];

export {
    SysPosBindingdata,
    SysPosForm,
    SysPosStatemachine,
    SysPosViewmodel,
    SysPosUistate,

    APP_PROVIDERS,
    commandHandlers
};



