import { Injectable, Injector } from '@angular/core';
import { FormControl } from '@angular/forms';
import { NgForm, Form, NgFormControl, NgChildForm, NgChildFormArray } from '@farris/devkit';
import { SysPosBindingdata } from './sys-post-bindingdata';

@Injectable()
@NgForm({
  bindingData: SysPosBindingdata
})
class SysPosForm extends Form {

  constructor(injector: Injector) {
    super(injector);
  }

  /**
   * id
   */
  @NgFormControl({
    name: 'id',
    binding: 'ID'
  })
  FID: FormControl;

  /**
   * code
   */
  @NgFormControl({
    name: 'code',
    binding: 'Code'
  })
  FCode: FormControl;

  /**
   * 名称
   */
  @NgFormControl({
    name: 'name',
    binding: 'Name'
  })
  FName: FormControl;

  /**
   * path
   */
  @NgFormControl({
    name: 'path',
    binding: 'Path'
  })
  FPath: FormControl;

    /**
   * layer
   */
  @NgFormControl({
    name: 'layer',
    binding: 'Layer'
  })
  FLayer: FormControl;

    /**
   * isdetail
   */
  @NgFormControl({
    name: 'isDetail',
    binding: 'Isdetail'
  })
  FIsdetail: FormControl;

    /**
   * sortorder
   */
  @NgFormControl({
    name: 'sortOrder',
    binding: 'Sortorder'
  })
  FSortorder: FormControl;

    /**
   * sysinit
   */
  @NgFormControl({
    name: 'sysInit',
    binding: 'Sysinit'
  })
  FSysinit: FormControl;

    /**
   * note
   */
  @NgFormControl({
    name: 'note',
    binding: 'Note'
  })
  FNote: FormControl;

    /**
   * 父级
   */
  @NgFormControl({
    name: 'parentid',
    binding: 'Parentid'
  })
  FParentid: FormControl;
}


export { SysPosForm };



