
import { Injectable } from '@angular/core';
import { CommandHandler, NgCommandHandler, TaskExecutionContext } from '@farris/devkit';
import { SysPosDataService } from '../../domain';
import { SysPosUistate } from '../sys-post-uistate';
import { SysPosStatemachine } from '../sys-post-statemachine';


@Injectable()
@NgCommandHandler({
  commandName: 'formLoad'
})
class FormLoadHandler extends CommandHandler {

  constructor(
    private dataService: SysPosDataService,
    private uiState: SysPosUistate,
    private stateMachine: SysPosStatemachine
  ) {
    super();
  }

  schedule() {

    // 数据加载
    this.addTask('loadData', () => {
        this.uiState.action = 'load';
        this.dataService.getdata();
    });

    // 状态迁移
    this.addTask('transitState', () => {
      const action = this.uiState.action;
      this.stateMachine['view']();
    });
  }
}

export { FormLoadHandler };



