import { Injectable, Injector } from '@angular/core';
import { NgBindingData, BindingData } from '@farris/devkit';
import { SysPosEntity, SysPosRepository } from '../domain';

@Injectable()
@NgBindingData({
  entity:     SysPosEntity,
  repository: SysPosRepository
})
class SysPosBindingdata extends BindingData {
  constructor(injector: Injector) {
    super(injector);
  }
}

export { SysPosBindingdata };



